body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.psg3:hover {
  animation-iteration-count: infinite;
  animation-name: example;
  animation-timing-function: linear;
  animation-duration: 0.5s;
  cursor: pointer;
}

@keyframes example {
  0% {  box-shadow: 1px 3px 5px;
    border-left:  5px solid rgb(255, 2, 2);
    border-top: 0px solid  ;
    border-bottom: 0px solid ;
    border-right: 0px solid  ;
  }

  25% {
      box-shadow: 1px 3px 5px;
    border-left: 0px solid  ;
    border-top:  5px solid rgb(215, 225, 214);
    border-bottom: 0px solid  ;
    border-right: 0px solid ;
  }

  50% {      box-shadow: 1px 3px 5px;
    border-left: 0px solid  ;
    border-top: 0px solid  ;
    border-bottom: 0px solid  ;
    border-right:  5px solid rgb(40, 233, 6);
  }

  75% {      box-shadow: 1px 3px 5px;
    border-left: 0px solid  ;
    border-top: 0px solid  ;
    border-bottom:  5px solid  ;
    border-right: 0px solid;
  }

  100% {      box-shadow: 1px 3px 5px;
    border-left:  5px solid rgb(232, 6, 6);
    border-top:  5px solid rgb(152, 144, 152);
    border-bottom:  5px solid rgb(10, 10, 10);
    border-right:  5px solid rgb(5, 250, 25);
  }
}

.psg1 {
  background-color: #6DA5C0;
}

@media (max-width: 768px) {
  .psg2 {
    margin: 2rem;
  }

}

.psg {
  background-color: rgb(13, 13, 14);
  color: wheat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}